    <p>PromisejsPromisePromise</p>

    <div>
      <pre>
            <div class="code">
    function want() {
        console.log('');
    }

    function fn(want) {
        console.log('');

        // Promise
        return new Promise(function(resolve, reject) {
            if (typeof want == 'function') {
                resolve(want);
            } else {
                reject('TypeError: '+ want +'')
            }
        })
    }

    fn(want).then(function(want) {
        want();
    })

    fn('1234').catch(function(err) {
        console.log(err);
    })</div>
