    <p>Promise</p>

    <p></p>

    <div>
      <pre>
            <div class="code">
    var fn = function(num) {
    return new Promise(function(resolve, reject) {
            if (typeof num == 'number') {
                resolve(num);
            } else {
                reject('TypeError');
            }
        })
    }

    fn(2).then(function(num) {
        console.log('first: ' + num);
        return num + 1;
    })
    .then(function(num) {
        console.log('second: ' + num);
        return num + 1;
    })
    .then(function(num) {
        console.log('third: ' + num);
        return num + 1;
    });

    // 
    first: 2
    second: 3
    third: 4</div>
